import axios from 'axios';
import { IAssortmentItem } from '../../models/IAssortmentItem';
import { AppDispatch } from '../store';
import { assortmentSlice } from './AssortmentSlice';

export const fetchAssortments = () => async (dispatch: AppDispatch) => {
  let url = 'https://order164.ru/json/assortment.json';
  try {
    dispatch(assortmentSlice.actions.assortmentsFetching());
    const response = await axios.get<IAssortmentItem[]>(url);
    dispatch(assortmentSlice.actions.assortmentsFetchingSuccess(response.data));
  } catch (e: any) {
    dispatch(assortmentSlice.actions.assortmentsFetchingError(e.message));
  }
};
